import { Display } from '@components/Display';
import ErrorBoundary from '@components/ErrorBoundary';
import { History } from '@components/History';
import { Keypad } from '@components/Keypad';
import { useTypedDispatch, useTypedSelector } from '@hooks/.';
import { SetStateAction } from '@interfaces/.';
import { clearHistory, setState } from '@store/.';
import { HomeContainer, HomeWrapper, HistoryContainer } from './components';

const Home = () => {
  const { displayValue, displayExpression, history } = useTypedSelector(
    (state) => state.calculator
  );
  const dispatch = useTypedDispatch();

  const handleKeypadClick = (action: SetStateAction) => dispatch(setState(action));
  const handleClearHistory = () => dispatch(clearHistory());

  return (
    <HomeContainer>
      <HomeWrapper>
        <ErrorBoundary fallback="Something wrong with display. Try to reload the page, please">
          <Display value={displayValue} expression={displayExpression} />
        </ErrorBoundary>
        <ErrorBoundary fallback="Something wrong with keypad. Try to reload the page, please">
          <Keypad onKeyClick={handleKeypadClick} />
        </ErrorBoundary>
      </HomeWrapper>
      <HistoryContainer data-test-id="history">
        <ErrorBoundary fallback="Something wrong with history. Try to reload the page, please">
          <History history={history} clearHistory={handleClearHistory} />
        </ErrorBoundary>
      </HistoryContainer>
    </HomeContainer>
  );
};

export default Home;
